import { useState, useEffect } from 'react'
import { useRouter } from 'next/router'
import { useAuthStore } from '@/stores/useAuthStore'
import { getDossiers, createDossier, type Dossier } from '@/services/dossierService'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { Button } from '@/components/ui/button'
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Plus, Loader2, FileText } from 'lucide-react'
import { toast } from 'sonner'

interface DossierSelectorProps {
  onDossierChange?: (dossierId: string | null) => void
}

export default function DossierSelector({ onDossierChange }: DossierSelectorProps) {
  const router = useRouter()
  const { user } = useAuthStore()
  const currentClinicId = user?.clinicId || null
  const [dossiers, setDossiers] = useState<Dossier[]>([])
  const [currentDossierId, setCurrentDossierId] = useState<string | null>(null)
  const [loadingDossiers, setLoadingDossiers] = useState(false)
  const [creatingDossier, setCreatingDossier] = useState(false)
  const [newDossierTitle, setNewDossierTitle] = useState('')
  const [isDialogOpen, setIsDialogOpen] = useState(false)

  useEffect(() => {
    if (currentClinicId) {
      loadDossiers()
    } else {
      setDossiers([])
      setCurrentDossierId(null)
    }
  }, [currentClinicId])

  const loadDossiers = async () => {
    if (!currentClinicId) return

    setLoadingDossiers(true)
    try {
      const allDossiers = await getDossiers(currentClinicId)
      setDossiers(allDossiers)
      
      // Se no h dossi selecionado e h dossis disponveis, selecionar o primeiro
      if (!currentDossierId && allDossiers.length > 0) {
        const firstDossier = allDossiers[0]
        setCurrentDossierId(firstDossier.id)
        onDossierChange?.(firstDossier.id)
      }
    } catch (error) {
      console.error('Erro ao carregar dossis:', error)
      toast.error('Erro ao carregar lista de dossis')
    } finally {
      setLoadingDossiers(false)
    }
  }

  const handleSelectDossier = (dossierId: string) => {
    if (dossierId === currentDossierId) return
    setCurrentDossierId(dossierId)
    onDossierChange?.(dossierId)
  }

  const handleCreateDossier = async () => {
    if (!newDossierTitle.trim()) {
      toast.error('Digite o ttulo do dossi')
      return
    }

    if (!currentClinicId) {
      toast.error('Selecione uma clnica primeiro')
      return
    }

    setCreatingDossier(true)
    try {
      const newDossier = await createDossier(currentClinicId, newDossierTitle.trim())
      setNewDossierTitle('')
      setIsDialogOpen(false)
      await loadDossiers()
      setCurrentDossierId(newDossier.id)
      onDossierChange?.(newDossier.id)
      toast.success('Dossi criado com sucesso!')
      // Redirecionar para a primeira seo (Identity)
      router.push(`/dossie/${newDossier.id}/identity`)
    } catch (error: any) {
      console.error('Erro ao criar dossi:', error)
      toast.error(error.message || 'Erro ao criar dossi')
    } finally {
      setCreatingDossier(false)
    }
  }

  const currentDossier = dossiers.find((d) => d.id === currentDossierId)

  if (!currentClinicId) {
    return null
  }

  return (
    <div className="flex items-center gap-2">
      <FileText className="size-4 text-slate-500" />
      <Select
        value={currentDossierId || ''}
        onValueChange={handleSelectDossier}
        disabled={loadingDossiers}
      >
        <SelectTrigger className="w-[250px]">
          <SelectValue placeholder="Selecione um dossi" />
        </SelectTrigger>
        <SelectContent>
          {dossiers.length === 0 ? (
            <SelectItem value="none" disabled>
              Nenhum dossi cadastrado
            </SelectItem>
          ) : (
            dossiers
              .filter((d) => d.status !== 'archived')
              .map((dossier) => (
                <SelectItem key={dossier.id} value={dossier.id}>
                  {dossier.title}
                  {dossier.status === 'finalized' && ' '}
                </SelectItem>
              ))
          )}
        </SelectContent>
      </Select>

      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogTrigger asChild>
          <Button variant="outline" size="sm">
            <Plus className="mr-2 size-4" />
            Novo Dossi
          </Button>
        </DialogTrigger>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Criar Novo Dossi</DialogTitle>
            <DialogDescription>
              Crie um novo dossi para documentar a estratgia da clnica. Cada dossi representa um snapshot no tempo.
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="dossier-title">Ttulo do Dossi</Label>
              <Input
                id="dossier-title"
                value={newDossierTitle}
                onChange={(e) => setNewDossierTitle(e.target.value)}
                placeholder="Ex: Dossi 2026 - Q1"
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    handleCreateDossier()
                  }
                }}
              />
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
              Cancelar
            </Button>
            <Button onClick={handleCreateDossier} disabled={creatingDossier}>
              {creatingDossier ? (
                <>
                  <Loader2 className="mr-2 size-4 animate-spin" />
                  Criando...
                </>
              ) : (
                'Criar Dossi'
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}


